<template>
	<div id="good-detail">
		<div>
			<div class="header">
				<img class="arrow-icon fl-left" src="../../assets/common_btn_arrow2@2x.png" @click="$router.go(-1)"/>
				<img class="share-icon fl-right" src="../../assets/common_btn_share@2x.png"/>
				<img class="cart-icon fl-right" src="../../assets/common_btn_shopping_cart@2x.png" @click="toCart"/>
			</div>
			<van-swipe :autoplay="3000">
				<van-swipe-item v-for="(image, index) in goods.images" :key="index">
					<img :src="image.imagePath" class="swipe-img" alt="" style="height: 10rem;"/>
				</van-swipe-item>
			</van-swipe>
			
			<div class="detail-info mb20">
				<div class="goods-name" v-if="goods.orgSummery">
					<span class="name-label" v-if="goods.orgSummery.orgId==0">自营</span>
					<span class="name-label" v-if="goods.orgSummery.orgId!=0">加盟</span>
					{{goods.name}}
				</div>
				<div class="prices">
					<span class="price-label">抢购价</span>
					<span class="price">￥ <em>{{goodsImmediately.discountPrice}}</em></span>
					<del class="oprice">￥{{goodsImmediately.originalPrice}}</del> 
				</div>
				
				<div class="tag-list clearfix" v-for="item in goods.tags">
					<div class="tag-item">{{item.name}}</div>
				</div>
			</div>
			
			<template v-if="type==1">
				<div class="explain mb20" >
					定制说明
					<img src="../../assets/arrow_gray.png" class="arrow_gray">
				</div>
			</template>
			
			<template v-if="type==2">
				<div class="explain mb20" @click="chooseNorms">
					选择规格尺寸
					<img src="../../assets/arrow_gray.png" class="arrow_gray">
				</div>
				<norms 
					v-on:hideMask="hideMask" 
					:showMask="showMask" 
					:goodsImmediately="goodsImmediately"
					:specList="specList"
					:sizeList="sizeList"
					:goodsInfo="goods"
					v-if= "(specList.length > 0 || sizeList.length > 0) && goodsImmediately"></norms>
			</template>
			
			<div class="comment mb20" v-if="goods.praiseSummery">
				<div class="comment-bar">
					用户评价({{goods.praiseSummery.praiseCount}})
					<!--<img src="../../assets/arrow_gray.png" class="arrow_gray">-->
					<div class="start_sel">
						<i class="star" v-bind:class="'star-'+ parseInt(goods.praiseSummery.praiseValue)"></i><span>{{goods.praiseSummery.praiseValue}}分</span>
					</div>
				</div>
				<div class="comment-main">
					<div class="list-item clearfix">
						<div class="item-img fl-left">
							<img :src="goods.comment.imagePath" alt="">
						</div>
						<div class="item-info fl-left">
							<h4 class="text-over">{{goods.comment.name}}</h4>
							<p>{{goods.comment.comTime}}</p>
							<div class="start_sel">
								<div class="star" v-bind:class="'star-'+ parseInt(goods.comment.startNum)"></div>
							</div>
						</div>
					</div>
					<p class="comment-desc">{{goods.comment.content}}</p>
					<p class="good-title text-over">{{goods.name}}</p>

					<div class="comment-more" @click="toCommentList">查看全部评价</div>
				</div>
			</div>
			<div class="store mb20" v-if="goods.orgSummery">
				<router-link :to="{ name : 'ShopGoodsList' , params: { id: goods.orgSummery.orgId }}">
					<div class="list-item clearfix">
						<div class="item-img fl-left">
							<img :src="goods.orgSummery.iconPath" alt="">
						</div>
						<div class="item-info fl-left">
							<h4>{{goods.orgSummery.orgName}}</h4>
							<p>{{goods.orgSummery.description}}</p>
						</div>
						<img src="../../assets/arrow_gray.png" class="arrow_gray">
					</div>
				</router-link>
			</div>
			<div class="good-article">
				<h4>商品详情</h4>
				<div class="article-main" v-for="item in article">{{item.content}}</div>
			</div>
		</div>
		
		<!--定制的底部-->
		<template v-if="type==1">
			<div class="btn">
				<div class="service fl-left">
					<img src="../../assets/ico_service.png">
					<span>客服</span>
				</div>
				<div class="customized-btn fl-left">我要定制</div>
			</div>
		</template>
		
		<!--普通商品底部-->
		<template v-if="type==2">
			<div class="btn2">
				<div class="service fl-left">
					<img src="../../assets/ico_service.png">
					<span>客服</span>
				</div>
				<div class="add-btn btns fl-left" @click="addCart">加入购物车</div>
				<div class="send-btn btns fl-left" @click="sendToFriend">送给好友</div>
			</div>
		</template>
	</div>
</template>

<script>
	import Norms from '@/components/Norms/Norms'
	
	export default {
		data() {
			return {
				type: 2,
				enterType:'',
				showMask:false,
				article:[],
//				images: [
//					'../../../static/demo/timg1.jpg',
//					'../../../static/demo/timg2.jpg',
//					'../../../static/demo/timg3.jpg',
//				],
				specList:[],
				sizeList:[],
				goods:{
//					skuId:1,
//					spuId:1,
//					type:1,
//					promId:1,
//					name:'酷美人 夜光石手链 男女款萤石手链手串手环饰品萤石手链手串手环饰品',
//					spec:'规格',
//					size:'尺寸',
//					number:'001',
//					placeName:'产地',
//					tags:[{id:1,name:'标签'}],
//					images:[{id:1,imagePath:'../../../static/demo/timg1.jpg'},{id:2,imagePath:'../../../static/demo/timg2.jpg'}],
//					orgSummery:{orgId:1,orgName:'巴黎欧莱雅官方旗舰店',iconPath:'../../../static/demo/timg1.jpg',description:'定制商品数：2560'},
//					praiseSummery:{praiseCount:10,praiseValue:4.5},
//					comment:{id:1,name:'评论人',imagePath:'../../../static/demo/timg1.jpg',content:'包装非常漂亮，送货速度很快。强烈推荐购买，物超所值，性价比高。',comTime:'2017-02-03',praiseValue:4}
				},
				goodsImmediately:{
//					skuId:1,
//					promId:1,
//					reminder:'温馨提示',
//					isUp:1,
//					isSoldOut:0,
//					originalPrice:200,
//					discountPrice:100,
//					discount:0.5,
//					store:10,
				},
			}
		},
		components: {
	  	Norms
	  },
		watch: {},
		created(){
			this.getGoodsDetail()
			this.getGoodsImmediatelyDetails()
		},
		methods: {
			toCart(){
				this.$router.push({name:'Cart'})
			},
			toCommentList(){
				let skuid = this.goods.skuId
				this.$router.push({name:'CommentList',params:{id:skuid} })
			},
			chooseNorms(){
				this.showMask = true
				this.enterType = 1
			},
			addCart(){
				this.showMask = true
				this.enterType = 2
				
			},
			sendToFriend(){
				this.showMask = true
				this.enterType = 3
			},
			hideMask(goods){
				console.log(goods)
				this.showMask = false
				let enterType = this.enterType
				if(goods != 'cancel'){
					switch (enterType){
						case 1:
							console.log(1)	
							break;
						case 2:
							console.log(2)
							if(this.goodsImmediately.isUp != 1){
								this.$toast('商品已下架');
							}else if(this.goodsImmediately.isSoldOut == 1){
								this.$toast('商品已售罄');
							}else{
								this.getCartUpdate(goods)
							}
							break;
						case 3:
							if(this.goodsImmediately.isUp != 1){
								this.$toast('商品已下架');
							}else if(this.goodsImmediately.isSoldOut == 1){
								this.$toast('商品已售罄');
							}else{
//								this.$toast('送给好友');
      					this.$router.push({name: 'ConfirmOrder'})
								
							}
							console.log(3)	
							break;
						default:
							break;
					}
				}
			},
			
			
			getGoodsDetail(){
				let skuId = this.$route.query.skuId
				let promId = this.$route.query.promId
				this.$Api.getGoodsDetails(skuId,promId).then((res)=>{
					if(res.q.s == 0){
						this.goods = res.q.goods
						this.sizeList = res.q.sizeList
						this.specList = res.q.specList
						this.getArticleDetails(res.q.goods.articleId)
					}
				})
			},
			getGoodsImmediatelyDetails(){
				let skuId = this.$route.query.skuId
				this.$Api.getGoodsImmediatelyDetails(skuId).then((res)=>{
					console.log(res)
					if(res.q.s==0){
						this.goodsImmediately = res.q.goodsImmediately
					}
				})
			},
			getCartUpdate(params){
				let goods = {
					skuId:params.skuId,
					spuId:params.spuId,
					num : params.num,
					promId : params.promId,
					packingId : params.packingId,
				}
				let goodsList = []
				goodsList.push(goods)
				this.$Api.getCartUpdate(1,goodsList).then((res)=>{
					if(res.q.s==0){
						this.$toast('加入购物车成功');
					}else{
						this.$toast(res.q.d);
						
					}
				})
			},
			getArticleDetails(id){
				this.$Api.getArticleDetails(id).then((res)=>{
					console.log(res)
					if(res.q.s==0){
						this.article = res.q.article
					}
				})
			},
		},
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#good-detail {
		padding-bottom: box(100);
		background: #f0f0f0;
		.header{
			position:absolute;
			width: 100%;
			height: 1.17rem;
			line-height: 1.17rem;
			z-index: 999;
			>img{
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				width: 0.88rem;
				height: 0.88rem;
			}
			.arrow-icon{
				left: 0.4rem;
			}
			.share-icon{
				right: 0.4rem;
			}
			.cart-icon{
				right: 1.6rem;
			}

		}
		.swipe-img {
			width: 100%;
			hegiht: 10rem !important;
		}
		.van-swipe__indicator--active {
			background-color: #de7fef;
		}
		.detail-info {
			background: #fff;
			width: 100%;
			padding: box(30);
			font-size: box(30);
			.goods-name {
				line-height: box(40);
				color: #1a1a1a;
				font-size: box(30);
			}
			.name-label{
				@include goodslabel;
				margin-right: box(10);
			}
			.prices{
				margin: box(25) 0;
				font-size: 0;
				.price-label{
					font-size: box(24);
					color: #fff;
					background: #fd85ac;
					height: box(30);
					line-height: box(30);
					border-radius: box(4);
					text-align: center;
					padding: box(3) box(6);
					margin-right: box(14);
					vertical-align: text-bottom;
				}
				.price {
					color: #fe4436;
					font-size: box(30);
					font-weight: 800;
					>em{
						font-size: box(40);
					}
				}
				.oprice{
					margin-left: box(20);
					font-size: box(26);
					color: #999;
					
				}
			}
			.tag-list {
				.tag-item {
					float: left;
					height: box(40);
					border-radius: box(20);
					border: solid box(2) #de7fef;
					padding: 0 box(20);
					text-align: center;
					line-height: box(36);
					margin-right: box(20);
					margin-bottom: box(15);
					font-size: box(28);
					color: #de7fef;
				}
			}
		}
		.explain,
		.comment-bar {
			height: box(90);
			width: 100%;
			padding: 0 box(30);
			line-height: box(90);
			position: relative;
			color: #323232;
			font-size: box(32);
			background: #fff;
			.start_sel {
				float : right;
				position: relative;
				>span{
					font-size: 0.37rem;
				}
			}
			.star{
				display: inline-block;
				vertical-align: initial;
				width: box(176);
				height: box(26);
				background: url("../../assets/star.png") no-repeat;
				background-size: cover;
			}
		}
		.comment-bar {
			border-bottom: solid #ccc box(1);
		}
		.comment {
			padding-bottom: box(1);
			background: #fff;
			.list-item {
				display: flex;
				width: 100%;
				height: box(180);
				padding: box(30);
				.item-img {
					width: box(120);
					height: box(120);
					img {
						width: box(120);
						height: box(120);
						border-radius: box(60);
					}
				}
				.item-info {
					/*width: box(540);*/
					flex:1;
					height: box(120);
					position: relative;
					padding: box(15) 0;
					margin-left: box(30);
					color: #323232;
					h4 {
						font-size: box(32);
						width: box(240);
						overflow: hidden;
						text-overflow: ellipsis;
					}
					p {
						font-size: box(28);
						position: absolute;
						bottom: 0;
						left: 0;
						color: #666;
					}
					.start_sel {
						width: box(176);
						/*height: box(26);*/
						overflow: hidden;
						position: absolute;
						top: box(30);
						right: 0;
					}
					.star {
						display: inline-block;
						vertical-align: initial;
						width: box(176);
						height: box(26);
						background: url("../../assets/star.png") no-repeat;
						background-size: cover;
					}
				}
			}
			.comment-desc {
				font-size: box(28);
				color: #323232;
				/*padding: box(15) box(30) box(20);*/
				padding: 0 box(30) box(22) box(30);
			}
			.good-title {
				font-size: box(26);
				color: #666;
				padding: 0 box(30);
			}
			.comment-more {
				width: box(240);
				height: box(60);
				border-radius: box(30);
				border: solid box(2) #de7fef;
				line-height: box(60);
				text-align: center;
				color: #de7fef;
				font-size: box(26);
				margin: box(50) auto;
			}
		}
		.arrow_gray {
			@include rt-arrow;
		}
		.mb20 {
			margin-bottom: box(20);
		}
		.store {
			background: #fff;
			.list-item {
				width: 100%;
				height: box(220);
				position: relative;
				padding: box(30);
				.item-img {
					width: box(220);
					height: box(160);
					position: relative;
					img {
						width: box(220);
						height: box(160);
						border-radius: box(8);
					}
				}
				.item-info {
					width: box(440);
					height: box(160);
					position: relative;
					margin-left: box(30);
					color: #1a1a1a;
					h4 {
						font-size: box(30);
					}
					p {
						font-size: box(28);
						position: absolute;
						bottom: 0;
						left: 0;
					}
				}
			}
		}
		.good-article {
			width: 100%;
			padding: box(30);
			background: #fff;
			h4 {
				font-size: box(32);
				color: #333;
				margin-bottom: box(30);
			}
			.article-main {}
		}
		.btn {
			width: 100%;
			height: box(98);
			border-top: box(2) solid #ccc;
			position: fixed;
			bottom: 0;
			left: 0;
			text-align: center;
			line-height: box(96);
			.service {
				width: box(200);
				height: box(96);
				line-height: box(10);
				background: #fff;
				img {
					width: box(44);
					display: block;
					margin: box(10) auto;
				}
				span {
					font-size: box(24);
					color: #666;
				}
			}
			.customized-btn {
				width: box(550);
				height: box(96);
				background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
				color: #fff;
				font-size: box(34);
			}
		}
		.btn2 {
			width: 100%;
			height: box(98);
			border-top: box(2) solid #ccc;
			position: fixed;
			bottom: 0;
			left: 0;
			text-align: center;
			line-height: box(96);
			display: flex;
			.service {
				flex: 0 0 box(200);
				height: box(96);
				line-height: box(10);
				background: #fff;
				img {
					width: box(44);
					display: block;
					margin: box(10) auto;
				}
				span {
					font-size: box(24);
					color: #666;
				}
			}
			.btns {
				flex: 0 0 box(275);
				height: box(96);
				color: #fff;
				font-size: box(34);
			}
			.add-btn {
				background: #fe9bbb;
			}
			.send-btn {
				background: #de7fef;
			}
		}
	}
</style>


